/*==============================================================================
JavaScript Macro - Magic Stringify
	Author & Contributor:
		hugoalh
	Language:
		JavaScript 6
==============================================================================*/
export default function(Data) {
	switch (typeof Data) {
		case "bigint": case "boolean": case "number": case "string": case "undefined":
			break;
		case "function": case "object": case "symbol": default:
			Data = JSON.stringify(Data);
			break;
	};
	return Data;
};
